.FollowServiceForm {
    display: grid;
    flex-direction: column;
    justify-content: flex-end;
    line-height: 2;
}

.buttonFS {
    height: 50px;
    width: 505px;
    border-radius: 12px;
    border-color: rgb(0, 142, 95);
}

.followH1 {
    text-align: left;
}

.FollowServiceFormDiv {
    width: 50%;
    padding: 1%;
}

.ContentFollowService {
    padding-left: 20%;
}

.FollowServiceAll {
    background-image: url(../modules/img/bgc.png);
    background-repeat: no-repeat;
    background-size: 100%;
    height: 100%;
    width: 100%;
    display: flex;
    margin-top: -2%;
}


.inpurFollowServText {
    height: 50px;
    width: 500px;
    margin-bottom: 3%;
    border-radius: 12px;
    padding-top: 1%;
    border-color: rgb(0, 142, 95);
    border-width: 1px;
    border-top-color: black;
    border-left-color: black;
}

.inpurFollowServ {
    height: 50px;
    width: 500px;
    margin-bottom: 3%;
    border-radius: 12px;
    border-color: rgb(0, 142, 95);
    border-width: 1px;
    padding-left: 2%;
}

.aAssignment {
    color: white;
}

@media screen and (max-width: 1500px) {
    .FollowServiceAll {
        background-size: 190%;
        height: 190%;
        width: 100%;
        display: flex;
        margin-top: -2%;
    }
}

@media screen and (max-width: 1000px) {
    .ContentFollowService {
        padding-left: 0;
    }

    .inpurFollowServ {
        height: 50%;
        width: 60%;
    }

    .inpurFollowServText {
        height: 20%;
        width: 75%;
    }

    .FollowServiceForm {
        display: grid;
    }

    .FollowServiceAll {
        background-size: 190%;
        height: 190%;
        width: 100%;
        display: flex;
        margin-top: -2%;
    }

    .buttonFS {
        height: 100%;
        width: 60%;
        border-radius: 12px;
        margin-left: 20%;
        border-color: rgb(0, 142, 95);
    }

    .labelcheckboxFollow {
        line-height: 1.5;
        text-align: left;
        padding-left: 18%;
        margin-bottom: 10%;
    }
}

@media screen and (max-width: 800px) {
    .ContentFollowService {
