        margin-bottom: 10%;
    }
}

@media screen and (max-width: 600px) {
    .FollowServiceAll {
        background-size: 2200px;
        height: 100%;
        width: 100%;
        display: flex;
        margin-top: -2%;
    }

    .inpurFollowServText {
        height: 20%;
        width: 75%;
    }

    .buttonFS {
        margin-left: 90%;
    }

    .FollowServiceAll {
        display: grid;
        grid-template-columns: repeat(1, 1fr);

    }

    .FollowServiceFormDiv {
        margin-left: 20%;
    }

    .ContentFollowService {
        padding-left: 10%;
    }

    .inpurFollowServ {
        height: 50%;
        width: 100%;
        padding-left: 10%;
    }

    .inpurFollowServText {
        height: 20%;
        width: 110%;
    }



    .buttonFS {
        height: 30px;
        width: 60%;
        border-radius: 12px;
        margin-left: 20%;
        margin-top: 10%;
        margin-bottom: 10%;
        border-color: rgb(0, 142, 95);
    }

    .labelcheckboxFollow {
        line-height: 1.5;
        text-align: left;
        margin-bottom: 10%;
        margin-left: -22%;
    }
}

