.modal {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.25);
    animation-name: appear;
    animation-duration: 300ms;
}

.btnMySelf {
    background: none;
    border: none;
    font-size: 15px;
    color: rgb(128, 128, 128);
}

.btnMySelf:hover {
    color: rgb(42, 190, 200);
}

.modal-dialog {
    width: 100%;
    max-width: 550px;
    background: rgb(255, 255, 255);
    position: relative;
    margin: 0 20px;
    max-height: calc(100vh - 40px);
    text-align: left;
    display: flex;
    border-radius: 20px;
    flex-direction: column;
    overflow: hidden;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -webkit-animation-name: animatetop;
    -webkit-animation-duration: 0.4s;
    animation-name: slide-in;
    animation-duration: 0.5s;
    padding-bottom: 2%;
}

.modal-header,
.modal-footer {
    display: flex;
    align-items: center;
    padding: 1rem;
}

.modal-header {
    border-bottom: 1px solid #dbdbdb;
    justify-content: space-between;
}

.modal-footer {
    border-top: 1px solid #dbdbdb;
    justify-content: flex-end;
}

.modal-close {
    cursor: pointer;
    padding: 1rem;
    margin: -1rem -1rem -1rem auto;
}

.modal-body {
    overflow: auto;
}

.modal-content {
    padding: 1rem;
}

.FooterTimelistBtns {
    display: flex;
    justify-content: space-around;
}

.SaveBtnTimelist {
    background-color: rgb(42, 190, 200);
    width: 200px;
    height: 40px;
    border-radius: 10px 10px 10px 10px;
    border: none;
    color: white;
    margin-right: 100px;
}

.DiscardBtnTimelist {
    background-color: rgb(238, 1, 1);
    width: 200px;
    height: 40px;
    border-radius: 10px 10px 10px 10px;
    border: none;
    color: white;
    margin-right: 10px;
}

@keyframes appear {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes slide-in {
    from {
        transform: translateY(-150px);
    }

    to {
        transform: translateY(0);
    }
}